.wrapper {
  max-width: 400px;
  width: 80vw;
  height: 90vh;
  max-height: 1200px;
  margin: auto;
  background: #002EA6;
  border-radius: 15px;
  box-shadow: 0 0 3px 4px #b4b4b4;
}

@media (max-width: 500px) {
  .wrapper {
    width: 100vw;
    height: 100vh;
    border-radius: 0;
  }

}

.siteHeader {
  padding: 16px;
  font-size: 18px;
}


.calender {
  margin: 16px 16px 16px 16px;
  background: white;
  border-radius: 24px;
  color: #333;
}

.calender-header {
  text-align: center;
  padding: 8px 0;
  font-size: 18px;
}

.calender-weekdays {
  display: flex;
  justify-content: space-around;
}

.calender-days {
  display: flex;
  flex-wrap: wrap;
}

.calender-days > li {
  text-align: center;
  width: calc(100% / 7);
  height: 42px;
  line-height: 42px;
  border: 1px solid transparent;
  position: relative;
}

.calender-days > li.calender-days-selected {
  border-radius: 8px;
  border: 1px solid #FFE78F;
}

.calender-days > li.calender-days-disabled {
  color: #999
}

.calender-days > li.calender-days-hasEvents::after {
  content: '';
  border: 1px solid green;
  position: absolute;
  top: 4px;
  right: 4px;
  width: 8px;
  height: 8px;
  background: green;
  border-radius: 50%;
}

.calender-days-today {
  border-radius: 8px;
  background: #FFE78F;
}

.calender-actions {
  display: flex;
  justify-content: space-between;
  padding: 16px;
}

.calender-actions button {
  font-size: 12px;
  height: 24px;
  padding: 0 8px;
}

.calender-actions-right {
  display: flex;
}

.events-wrapper {
  padding: 16px;
}

.events-item {
  padding: 4px 0;
}